<template>
  <div id="app">
    <div class="map container">
      <l-map
        @click="onCustomClick"
        :zoom="zoom"
        :center="center"
        style="height: 100%"
      >
        <l-tile-layer
          :url="url"
          :attribution="attribution"
        />
        <l-marker
          v-if="coord.lat != ''"
          ref="marker"
          :lat-lng="getCoord(coord.lat,coord.long)">
          <l-icon :icon-url="coord.icon" :icon-size="coord.iconSize" />
          <l-popup :content="coord.content"></l-popup>
        </l-marker>(
      </l-map>
    </div>
    <div class="form container">
      <form ref="form" method="get">
        <div>
          <h3>Select Coordinates or City</h3>
          <div>
            <div>
              <input type="number" step="0.0001" name="latitude" id="latitude" v-model="coord.lat" @change="getData(getCoord(coord.lat,coord.long))">
              <label for="latitude">Latitude</label>
            </div>
          </div>
          <div>
            <div>
              <input type="number" step="0.0001" name="longitude" id="longitude" v-model="coord.long" @change="getData(getCoord(coord.lat,coord.long))">
              <label for="longitude">Longitude</label>
            </div>
          </div>
          <div>
            <div>
              <button type="button" @click="getLocationGPS()">Detect GPS Position</button>
            </div>
          </div>
        </div>
        <input class="weatherUrl" type="text" rel="link" readonly="readonly" v-model="weatherUrl">
        <a :href="weatherUrl" target="_blank">open link</a>
        <chart v-if="response != undefined" :chartData="response"></chart>
        <div class="settings">
          <h3>Settings</h3>
          <div class="col-12">
            <div>
              <input v-model="settings.settings.current" type="checkbox" id="current_weather" name="current_weather" value="true" @change="getData(getCoord(coord.lat,coord.long))">
              <label for="current_weather">Current weather with temperature, windspeed and weather code</label>
            </div>
          </div>
          <div class="col-3">
            <div>
              <select v-model="settings.settings.Temperature" name="temperature_unit" id="temperature_unit" aria-label="Temperature Unit" data-default="celsius" @change="getData(getCoord(coord.lat,coord.long))">
                <option selected="" value="celsius">Celsius °C</option>
                <option value="fahrenheit">Fahrenheit °F</option>
              </select>
              <label for="temperature_unit">Temperature Unit</label>
            </div>
          </div>
          <div class="col-3">
            <div>
              <select v-model="settings.settings.Wind" name="windspeed_unit" id="windspeed_unit" aria-label="Windspeed Unit" data-default="kmh" @change="getData(getCoord(coord.lat,coord.long))">
                <option selected="" value="kmh">Km/h</option>
                <option value="ms">m/s</option>
                <option value="mph">Mph</option>
                <option value="kn">Knots</option>
              </select>
              <label for="windspeed_unit">Wind Speed Unit</label>
            </div>
          </div>
          <div class="col-3">
            <div>
              <select v-model="settings.settings.Precipitation" name="precipitation_unit" id="precipitation_unit" aria-label="Precipitation Unit" data-default="mm" @change="getData(getCoord(coord.lat,coord.long))">
                <option selected="" value="mm">Millimeter</option>
                <option value="inch">Inch</option>
              </select>
              <label for="precipitation_unit">Precipitation Unit</label>
            </div>
          </div>
          <div class="col-3">
            <div>
              <select v-model="settings.settings.Timeformat" name="timeformat" id="timeformat" aria-label="Timeformat" data-default="iso8601" @change="getData(getCoord(coord.lat,coord.long))">
                <option selected="" value="iso8601">ISO 8601 (e.g. 2021-01-01)</option>
                <option value="unixtime">Unix timestamp</option>
              </select>
              <label for="timeformat">Timeformat</label>
            </div>
          </div>
          <div class="col-3">
            <div>
              <select v-model="settings.settings.Timezone" name="timezone" id="timezone" aria-label="Timezone" data-default="UTC" @change="getData(getCoord(coord.lat,coord.long))">
                <option value="America/Anchorage">America/Anchorage</option>
                <option value="America/Los_Angeles">America/Los_Angeles</option>
                <option value="America/Denver">America/Denver</option>
                <option value="America/Chicago">America/Chicago</option>
                <option value="America/New_York">America/New_York</option>
                <option value="America/Sao_Paulo">America/Sao_Paulo</option>
                <option selected="" value="UTC">UTC</option>
                <option value="Europe/London">Europe/London</option>
                <option value="Europe/Berlin">Europe/Berlin</option>
                <option value="Europe/Moscow">Europe/Moscow</option>
                <option value="Africa/Cairo">Africa/Cairo</option>
                <option value="Asia/Bangkok">Asia/Bangkok</option>
                <option value="Asia/Singapore">Asia/Singapore</option>
                <option value="Asia/Tokyo">Asia/Tokyo</option>
                <option value="Australia/Sydney">Australia/Sydney</option>
                <option value="Pacific/Auckland">Pacific/Auckland</option>
              </select>
              <label for="timezone">Timezone</label>
            </div>
          </div>
          <div class="col-3">
            <div>
              <select v-model="settings.settings.Past" name="past_days" id="past_days" aria-label="Past days" data-default="0" @change="getData(getCoord(coord.lat,coord.long))">
                <option selected="" value="0">0</option>
                <option value="1">1</option>
                <option value="2">2</option>
              </select>
              <label for="past_days">Past days</label>
            </div>
          </div>
        </div>
        <div>
          <h3>Hourly Weather Variables</h3>
          <div class="col-md-3">
            <div v-for="(item, index) in hourly" :key="index+item">
              <input type="checkbox" :value="item" :id="index+item" name="hourly" @change="selectHourly(item)">
              <label :for="index+item">
                {{ item.replaceAll('_',' ') }}
              </label>
            </div>
          </div>
        </div>
        <div>
          <h3>Daily Weather Variables <small class="text-muted">(*)</small></h3>
          <div class="col-md-6">
            <div v-for="(item, index) in daily" :key="index+item">
              <input type="checkbox" :value="item" :id="index+item" name="daily" @change="selectDaily(item)">
              <label :for="index+item">
                {{ item.replaceAll('_',' ') }}
              </label>
            </div>
          </div>
          <small class="text-muted">* Parameter timezone is mandatory</small>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
import { latLng ,Icon} from "leaflet";
import { LMap, LTileLayer, LMarker, LPopup, LTooltip,LIcon } from "vue2-leaflet";
import chart from "@/components/chart";
export default {
  name: 'App',
  components: { chart, LMap, LTileLayer, LMarker, LPopup, LTooltip, LIcon},
  data() {
    return {
      zoom: 18,
      center: latLng(39.903416, 32.858900),
      // url: 'https://tile.thunderforest.com/cycle/{z}/{x}/{y}.png?apikey=b90e089c365242a3a08dbb49a7084a61',
      url: 'http://{s}.tile.osm.org/{z}/{x}/{y}.png',
      weatherUrl: 'https://api.open-meteo.com/v1/forecast?',
      attribution: 'some text here',
      coord: { lat: '', long: '' },
      response: {},
      hourly: [
        'temperature_2m',
        'cloudcover',
        'windspeed_10m',
        'soil_temperature_0cm',
        'relativehumidity_2m',
        'cloudcover_low',
        'windspeed_80m',
        'soil_temperature_6cm',
        'dewpoint_2m',
        'cloudcover_mid',
        'windspeed_120m',
        'soil_temperature_18cm',
        'apparent_temperature',
        'cloudcover_high',
        'windspeed_180m',
        'soil_temperature_54cm',
        'pressure_msl',
        'shortwave_radiation',
        'winddirection_10m',
        'soil_moisture_0_1cm',
        'precipitation',
        'direct_radiation',
        'winddirection_80m',
        'soil_moisture_1_3cm',
        'weathercode',
        'diffuse_radiation',
        'winddirection_120m',
        'soil_moisture_3_9cm',
        'snow_depth',
        'evapotranspiration',
        'winddirection_180m',
        'soil_moisture_9_27cm',
        'freezinglevel_height',
        'vapor_pressure_deficit',
        'windgusts_10m',
        'soil_moisture_27_81cm',
      ],
      daily: [
        'weathercode',
        'precipitation_sum',
        'temperature_2m_max',
        'precipitation_hours',
        'temperature_2m_min',
        'windspeed_10m_max',
        'apparent_temperature_max',
        'windgusts_10m_max',
        'apparent_temperature_min',
        'winddirection_10m_dominant',
        'sunrise',
        'shortwave_radiation_sum',
        'sunset',
      ],
      images: [
        {
          code: [0],
          path: 'img/wi-day-sunny.b7e1ba13.svg'
        },
        {
          code: [1,2,3],
          path: 'img/wi-day-sunny-overcast.7d533f2e.svg'
        },
        {
          code: [45, 48],
          path: 'img/wi-fog.57a5cbcb.svg'
        },
        {
          code: [51, 53, 55],
          path: 'img/wi-rain.44130c44.svg'
        },
        {
          code: [56, 57],
          path: 'img/wi-rain-mix.c47a0eef.svg'
        },
        {
          code: [61, 63, 65],
          path: 'img/wi-rain-mix.c47a0eef.svg'
        },
        {
          code: [66, 67],
          path: 'img/wi-rain-mix.c47a0eef.svg'
        },
        {
          code: [71, 73, 75],
          path: 'img/wi-showers.072fe053.svg'
        },
        {
          code: [77],
          path: 'img/wi-snow.b215ef27.svg'
        },
        {
          code: [80, 81, 82],
          path: 'img/wi-snowflake-cold.babbd220.svg'
        },
        {
          code: [85, 86],
          path: 'img/wi-snow-wind.c9d31909.svg'
        },
        {
          code: [95],
          path: 'img/wi-storm-showers.152d8c14.svg'
        },
        {
          code: [96, 99],
          path: 'img/wi-thunderstorm.59ea23f8.svg'
        },
      ],
      settings: {
        hourly: [],
        daily: [],
        settings: {
          current: true,
          Temperature: 'celsius',
          Wind: 'kmh',
          Precipitation: 'mm',
          Timeformat: 'iso8601',
          Timezone: 'UTC',
          Past: 0,
        }
      }
    };
  },
  methods:{
    getData(latlng){
      if (latlng == undefined) return
      this.coord =
        {
          id:1,
          lat: latlng.lat.toFixed(4),
          long: latlng.lng.toFixed(4),
          icon: require('leaflet/dist/images/marker-icon-2x.png'),
          iconSize:[25,41],
          content: ``
        }
      this.constructWeatherLink()
      this.center = latlng
      this.response = undefined
      fetch(this.weatherUrl)
        .then(response => response.json())
        .then((json)=>{
          this.response = json
          if (json.error != undefined) return
          let content = `
                     <h2><b>Current Status</b></h2>
                     <b>Time:</b> ${json.current_weather.time.replace('T', ' ')} ${this.settings.settings.Timezone}
                     <br><b>Temperature:</b> ${json.current_weather.temperature}  ${this.settings.settings.Temperature}
                     <br><b>Wind Direction:</b> ${json.current_weather.winddirection}
                     <br><b>Wind Speed:</b> ${json.current_weather.windspeed} ${this.settings.settings.Wind}
                  `
          if(json.hourly != undefined){
            content+='<h2><b>Hourly</b></h2>'
            for (let i = 0; i < this.settings.hourly.length; i++) {
              if(json.hourly[this.settings.hourly[i]] != undefined){
                content+=`<b>${this.settings.hourly[i].replaceAll('_', ' ')}:</b> ${this.getMiddleValue(json.hourly[this.settings.hourly[i]],json.current_weather.time,json.hourly['time'])}<br>`
              }
            }
          }
          if(json.daily != undefined){
            content+='<h2><b>Daily</b></h2>'
            for (let i = 0; i < this.settings.daily.length; i++) {
              if(json.daily[this.settings.daily[i]] != undefined){
                content+=`<b>${this.settings.daily[i].replaceAll('_', ' ')}:</b> ${this.getMiddleValue(json.daily[this.settings.daily[i]],json.current_weather.time.substr(0,10),json.daily['time'])}<br>`
              }
            }
          }
          let icon = ''
          for (let i = 0; i < this.images.length; i++) {
            for (let j = 0; j < this.images[i].code.length; j++) {
              if(this.images[i].code[j] == json.current_weather.weathercode){
                icon = this.images[i].path
              }
            }
          }
          this.coord =
            {
              id:1,
              lat: latlng.lat.toFixed(4),
              long: latlng.lng.toFixed(4),
              icon: icon,
              iconSize:[50,50],
              content: content
            }
          this.center = latlng
          setTimeout(()=>{
            this.$refs.marker.mapObject.openPopup()
          },100)
        });
    },
    selectHourly(value){
      for (let i = 0; i < this.settings.hourly.length; i++) {
        if(this.settings.hourly[i] == value){
          return this.settings.hourly.splice(i,1)
        }
      }
      this.settings.hourly.push(value)
      this.getData(this.getCoord(this.coord.lat,this.coord.long))
    },
    selectDaily(value){
      for (let i = 0; i < this.settings.daily.length; i++) {
        if(this.settings.daily[i] == value){
          return this.settings.daily.splice(i,1)
        }
      }
      this.settings.daily.push(value)
      this.getData(this.getCoord(this.coord.lat,this.coord.long))
    },
    getMiddleValue(array, time, timearray){
      let nums = []
      for (let i = 0; i < array.length; i++) {
        if(timearray[i] == time){
          nums.push(array[i])
        }
      }
      if (typeof(nums[0]) == "string"){
        return nums[0].substr(11, 5)
      }
      return nums.reduce((a, b) => (a + b)) / nums.length
    },
    constructWeatherLink(){
      let url = 'https://api.open-meteo.com/v1/forecast?'
      url= url+`latitude=${this.coord.lat}&longitude=${this.coord.long}`
      if(this.settings.hourly.length != 0){
        let hourly = '&hourly='
        for (let i = 0; i < this.settings.hourly.length; i++) {
          if(this.settings.hourly.length - 1 == i){
            hourly+=this.settings.hourly[i]
          }else{
            hourly+=this.settings.hourly[i]+','
          }
        }
        url+=hourly
      }
      if(this.settings.daily.length != 0){
        let daily = '&daily='
        for (let i = 0; i < this.settings.daily.length; i++) {
          if(this.settings.daily.length - 1 == i){
            daily+=this.settings.daily[i]
          }else{
            daily+=this.settings.daily[i]+','
          }
        }
        url+=daily
      }
      if(this.settings.settings.Temperature != undefined){
        url+=`&temperature_unit=${this.settings.settings.Temperature}`
      }
      if(this.settings.settings.Wind != undefined){
        url+=`&windspeed_unit=${this.settings.settings.Wind}`
      }
      if(this.settings.settings.Precipitation != undefined){
        url+=`&precipitation_unit=${this.settings.settings.Precipitation}`
      }
      if(this.settings.settings.Timeformat != undefined){
        url+=`&timeformat=${this.settings.settings.Timeformat}`
      }
      if(this.settings.settings.Timezone != undefined){
        url+=`&timezone=${this.settings.settings.Timezone}`
      }
      if(this.settings.settings.Past != undefined){
        url+=`&past_days=${this.settings.settings.Past}`
      }
      if(this.settings.settings.current == true){
        url+='&current_weather=true'
      }
      this.weatherUrl = url
    },
    getLocationGPS(){
      navigator.geolocation.getCurrentPosition(function(position) {
        setLatLong(position.coords.latitude,position.coords.longitude)
      })
      let setLatLong = (lat,long)=>{
        this.getData(latLng(lat,long))
      }
    },
    getCoord(a,b){
      return latLng(a,b)
    },
    onCustomClick(item){
      const {latlng}=item;
      this.getData(latlng)
    },
  }
}
</script>

<style>
#app{
  font-family: Arial;
  font-size: 18px;
  padding-top: 48px;
  padding-bottom: 48px;
  font-weight: 700;
  color: #01000D;
  background: #E5E5E5;
}
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.map{
  height: 80vh;
}
.k-chart{
  margin-top: 48px;
  width: 100%;
  overflow-x: scroll;
}
button{
  background: #F9F9F9;
  height: 56px;
  min-width: 272px;
  border: none;
  border-radius: 8px;
  color: #1D6BF3;
  font-family: inherit;
  font-size: inherit;
  cursor: pointer;
  transition: 0.6s;
  margin-top: 24px;
}
button:hover{
  background: #1D6BF3;
  color: #F9F9F9;
  transition: 0.3s;
}
.container{
  width: 100%;
}
.settings{
  display: grid;
  gap: 24px;
  padding: 24px 0;
}
input{
  padding: 12px 24px;
  min-width: 272px;
  margin-right: 12px;
  background: #F9F9F9;
  border: none;
  border-radius: 8px;
  color: #1D6BF3;
  font-family: inherit;
  font-size: inherit;
  cursor: pointer;
  transition: 0.6s;
  margin-top: 24px;
}
select{
  margin-right: 12px;
  padding: 12px 24px;
  min-width: 272px;
  background: #F9F9F9;
  height: 56px;
  border: none;
  border-radius: 8px;
  color: #1D6BF3;
  font-family: inherit;
  font-size: inherit;
  cursor: pointer;
  transition: 0.6s;
  margin-top: 24px;
}
.col-md-3{
  padding: 24px 0;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
}
.col-md-6{
  padding: 24px 0;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
}
input[type='checkbox']{
  min-width: 0;
  padding: 0;
}
.weatherUrl{
  width: 100%;
  margin-bottom: 24px;
}
@media screen and (max-width: 800px) {
  .col-md-3{
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
  .col-md-6{
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
}
@media screen and (max-width: 550px) {
  .col-md-3{
    display: grid;
    grid-template-columns: 1fr;
  }
  .col-md-6{
    display: grid;
    grid-template-columns: 1fr;
  }
}
@media screen and (min-width: 0px){
  .container{
    padding: 0 24px;
  }
}
@media screen and (min-width: 1000px){
  .container{
    padding: 0 72px;
    width: 100%;
  }
}
@media screen and (min-width: 1400px){
  .container{
    padding: 0 144px;
  }
}
.form{
  padding-top: 48px;
}
</style>