<template>
  <div class="chart">
    <canvas id="chart" width="3000px" height="400px"></canvas>
  </div>
</template>

<script>
import Chart from "chart.js"
import moment from 'moment'

export default {
  name: "chart",
  props: {
    chartData: {},
    chartType: null
  },
  data(){
    return{
      gradient: null,
      data:{
        labels:[],
        datasets:[],
      },
      options:{
        tooltips: { mode: 'index', intersect: false, yAlign: 'center'},
        scales: {
          yAxes: [],
        },
        responsive: false,
        maintainAspectRatio: false,
        legend: {
          align: 'start',
        },
        plugins: {
          colorschemes: {
            scheme: 'brewer.YlGnBu9'
          }
        }
      }
    }
  },
  mounted() {
    this.createChart()
  },
  watch:{
    changeData:{
      handler(value) {
        if (value.chartData == undefined) return;
        if(value.chartData.hourly == undefined || value.chartData.daily == undefined) return;
        this.data.datasets = [];
        if (value.chartType){
          this.chartDataGenerator(value.chartData.daily, value.chartData["daily_units"]);
          this.chartUnitsGenerator(value.chartData["daily_units"])
          this.options.responsive = true;
        }
        else {
          this.chartDataGenerator(value.chartData.hourly, value.chartData["hourly_units"])
          this.chartUnitsGenerator(value.chartData["hourly_units"])
          this.options.responsive = false;
        }
        this.createChart()
      }
    },
    deep: true
  },
  methods:{
    createChart(){
      const ctx = document.getElementById('chart');
      if (this.chartData == undefined) return;
      this.data.datasets = [];
      if (this.chartType){
        this.chartDataGenerator(this.chartData.daily, this.chartData["daily_units"]);
        this.chartUnitsGenerator(this.chartData["daily_units"])
        this.options.responsive = true;
      }
      else {
        this.chartDataGenerator(this.chartData.hourly, this.chartData["hourly_units"])
        this.chartUnitsGenerator(this.chartData["hourly_units"])
        this.options.responsive = false;
      }
      new Chart(ctx, {
        type: "line",
        data: this.data,
        options: this.options
      });
    },
    chartDataGenerator(value, units){
      if (value){
        this.data.labels = this.dateFormating(value.time);
        for (const key in value) {
          if (key !== 'time') {
            this.data.datasets.push({
                label: key.replaceAll('_',' '),
                data: value[key],
                yAxisID: units[key],
                fill: false,
              }
            )
          }
        }
      }

    },
    chartUnitsGenerator(value){
      this.options.scales.yAxes = [];
      for (const key in value){
        if (key !== 'time'){
          this.options.scales.yAxes.push({
            id: value[key],
            type: "linear",
            position: "left",
            scaleLabel: {
              display: true,
              labelString: value[key],
            },
            ticks: {
              stepSize: 1,
              maxTicksLimit: 8,
            }
          });
        }
      }
    },
    dateFormating(a){
      let time = [];
      for (const key in a) {
        if (this.chartType) time[key] = moment(a[key]).format('MMMM Do, YYYY');
        else time[key] = moment(a[key]).format('MMMM Do, YYYY - hh:mm A');
      }
      return time;
    }
  }
};
</script>

<style scoped>
.chart{
  width: 100%;
  overflow-x: scroll;
  overflow-y: hidden;
  height: 417px;
  margin-top: 48px;
  margin-bottom: 48px;
}
canvas{
  display: block;
  width: 3000px;
  height: 400px;
  aspect-ratio: auto 3000 / 400;
}
canvas[Attributes Style] {
  aspect-ratio: auto 3000 / 400;
}
</style>